import { SET_TOKEN } from './actionTypes';

const initialState = [
  {
    token: ''
  }
];

export default function todos(state = initialState, action) {
  switch (action.type) {
    case SET_TOKEN:
      return [
        ...state,
        {
          token: action.token
        }
      ];

    default:
      return state;
  }
}
